import { Routes, Route, NavLink, useNavigate } from "react-router-dom";
import Page1 from "./Components/Page1/Page1";
import Page2 from "./Components/Page2/Page2";
import Page2id from "./Components/Page2/Page2id";
import { useEffect, useState } from "react";

const api = {
  key: "adb3e547b13e79746fac1a5d9c5ca410",
  base: "https://api.openweathermap.org/data/2.5/"
}

function App() {
  const navigate = useNavigate()
 const [text, setText] = useState<string>('Click to this text')
  function clear() {
    setText("")
  }
  useEffect(()=>{
    navigate("/")
  }, [])

  return (
    <div>
      <div>
        {/* <NavLink style={{fontSize: "40px"}} onClick={clear} to={"/"}>{text}</NavLink> */}
      <div style={{width: "100%", height: "100%"}} >
        <Routes>
        <Route path={"/"} element={<Page1 api={api} />} />
        <Route path='/page-2' element={<Page2 api={api} />} />
        <Route path='/page-2/:id' element={<Page2id api={api} />} />
      </Routes>
      </div>
      
      
      
    </div>
    </div>
    
  );
}

export default App;
